.savanna {
  min-height: 70vh;
  min-width: 100%;

  .savanna-game {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 10px;
  }

  .menu {
    margin-bottom: 10px;

    .menu-wrapper {
      display: flex;
      justify-content: space-between;

      .rating{
        margin: 10px;
        width: 200px;
        height: 30px;
        border-radius: 10px;
        font-size: 1.5rem;
      }

      .btns-toolbar{
        margin: 5px;

        .btn-group {
          margin: 0 10px;
        }
      }
    }
  }

  .game-wrapper {
    height: 85%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    
    .question-wrapper{
      height: 100%;
      width: 50%;
      text-align: center;
      overflow: hidden;
    }

    .answers-wrapper{
      height: 100%;  
      width: 50%;

    .answers-btns {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100%;

      .word-answer{
        display: flex;
        height: fit-content;
        min-width: fit-content;
        text-transform: uppercase;
        width: 15%;
        margin: 7% 5%;
        font-size: 1.3rem;
        padding: 7px
      }

      .button-icons {
        margin-right: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
  

        .hotkey {
          width: 25px;
          height: 25px;
          font-weight: 450;
          background-color: #777;
          border-radius: 5px;
          margin-right: 10px;
        }
      }

    }
    }
  }
  
  .word-quest{
    font-size: 2rem;
    position: relative;
    left: 0vw;
    border-radius: 30px;
    text-transform: uppercase;
    padding: 20px;
    overflow: hidden;
  }

  .toast {
    max-width: 68%;
  

    button {
      margin: 10px;
    }
  }
  
  .wrong-words {
    font-size: 1.5rem;
  }

  .toast.show {
    position: fixed;
    top: 200px;
    width: 68%;
    z-index: 10;
    height: 65%;
    left: 15vw;
  }
  
  .result {
    font-size: 2rem;
    text-align: center;
  }
}